import React from 'react';

// Components
import Navigation from '../components/Navigation';
import Footer from '../components/Footer';

const Preise = () => {
    return(
        <div className="main-container">
            <Navigation />
            <div className="hintergrund-container">
                <h2>Unsere Preise</h2>
                <div className="preis-container">
                    <div className="preis-karte">
                        <h2>Paket - <span>I</span></h2>
                        <div className="preis-info">
                            <div className="preis-text">
                                <p>Eine einseitige Webpräsenz</p>
                                <p>Support bis zum Release</p>
                                <p>Zeitnaher Support nach <br></br>Release</p>
                            </div>
                            <p className="preis">ab <span>7.500$</span></p>
                        </div>
                    </div>
                    <div className="preis-karte">
                        <h2>Paket - <span>II</span></h2>
                        <div className="preis-info">
                            <div className="preis-text">
                                <p>Eine mehrseitige Webpräsenz</p>
                                <p>Support bis zum Release</p>
                                <p>Zeitnaher Support nach <br></br>Release</p>
                                <p>Dynamischer Inhalt nach Wahl <br></br>
                                (einfach)</p>
                            </div>
                            <p className="preis">ab <span>15.000$</span></p>
                        </div>
                    </div>
                    <div className="preis-karte">
                        <h2>Paket - <span>III</span></h2>
                        <div className="preis-info">
                            <div className="preis-text">
                                <p>Eine mehrseitige Webpräsenz</p>
                                <p>Support bis zum Release</p>
                                <p>zusätzlich 1 Monat kostenloser <br></br>Support nach Release</p>
                                <p>Dynamischer Inhalt<br></br>
                                    (Login, Listen, usw)
                                </p>
                            </div>
                            <p className="preis">ab <span>35.000$</span></p>
                        </div>
                    </div>
                </div>
            </div>
            <Footer />
        </div>
    );
};

export default Preise;