import React from 'react';

// Components
import VorschauNav from '../components/VorschauNav';

// Images
import BestWayProd from '../images/bestway-produkte.PNG';

const BestWayProdukte = () => {
    return(
        <div className="vorschau-container">
            <VorschauNav />
            <div className="background-second">
                <div className="vorschau-karte">
                    <h2>Vorschau - Dynamische Produktliste</h2>
                    <img className="bestwayprodukte" src={BestWayProd} alt=""/>
                </div>
            </div>
        </div>
    );
};

export default BestWayProdukte;