import React from 'react';

// Images
import Karte from '../images/büro.gif';

// Components
import Navigation from '../components/Navigation';
import Footer from '../components/Footer';

const Kontakt = () => {
    return(
        <div className="main-container">
            <Navigation />
            <div className="hintergrund-container">
                <h2>Kontakt</h2>
                <div className="kontakt-container">
                    <div className="kontakt">
                        <div className="karte-container">
                            <img src={Karte}/>
                        </div>
                        <div className="kontakt-info-container">
                            <p className="kontakt-heading"><span>CloudV</span> - Webentwicklung</p>
                            <div className="kontakt-info-top">
                                <p>Jakob Jameson</p>
                                <p>1822-64666006</p>
                                <p>Jakob_Jameson@statev.de</p>
                            </div>
                            <hr></hr>
                            <div className="kontakt-info-bottom">
                                <p>Popular Street 44</p>
                                <p>East Los Santos</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Footer />
        </div>
    );
};

export default Kontakt;