import React from 'react';

// Bilder
import Person from '../images/person-groß.png';
import StrichLinks from '../images/strich_links.svg';
import StrichRechts from '../images/strich_rechts.svg';

// Components
import Navigation from '../components/Navigation';
import Footer from '../components/Footer';

const Agentur = () => {
    return(
        <div className="main-container">
            <Navigation />
            <div className="hintergrund-container">
                <h2>Die Agentur</h2>
                <div className="agentur-container">
                    <div className="agentur-links">
                        <img src={StrichLinks} alt="Deko Strich" className="strich-links" />
                        <div className="person-container">
                            <img src={Person} alt="Entwickler Jakob Jameson" id="person-img"/>
                            <div className="person-text">
                                <h3><span>Jakob Jameson</span> - Webentwickler</h3>
                                <p>
                                    Seit mehreren Jahren setze ich nun mit der größten
                                    Begeisterung und Motivation die verschiedensten
                                    Projekte im Bereich Webapps und Websites um.<br></br><br></br>

                                    Sie möchten auch online aktiv sein aber wissen einfach
                                    nicht wo Sie anfangen sollen? Gerne helfe ich Ihnen hier
                                    von der ersten Planung an bis zum finalen Release Ihrer
                                    eigenen Website!
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className="agentur-rechts">
                        <img src={StrichRechts} alt="Deko Strich" className="strich-rechts" />
                        <div className="agentur-text">
                            <h3><span>CloudV</span> - Webagentur</h3>
                            <p>
                                Die CloudV Webagentur ist ein Teil der BestWay Unternehmensgruppe und
                                wurde im April 2021 von Jakob Jameson gegründet. Der Schwerpunkt der
                                Webagentur liegt in der Entwicklung von verschiedensten Websites sowie
                                Webapps.
                                <br></br><br></br>
                                Mit großer Motivation gehen wir an jedes einzelne Projekt um den Kunden
                                das optimale Ergebnis zu liefern.
                            </p>
                        </div>
                    </div>
                </div>
                <Footer />
            </div>
        </div>
    );
};

export default Agentur;