import React from 'react';
import { Link } from 'react-router-dom';

const VorschauNav = () => {
    return(
        <nav className="vorschau-nav">
            <ul>
                <li><Link to="/portfolio">zurück</Link></li>
                <li><Link to="/vorschau-bestway/navigation">Navigation</Link></li>
                <li><Link to="/vorschau-bestway/produkte">Produkte</Link></li>
                <li><Link to="/vorschau-bestway/standort">Standort</Link></li>
            </ul>
        </nav>
    );
};

export default VorschauNav;