import React from 'react';
import { Link } from 'react-router-dom';

// Components
import Navigation from '../components/Navigation';
import StrichLinks from '../images/strich_links.svg';
import StrichRechts from '../images/strich_rechts.svg';
import BestWay from '../images/bestway.png';
import Footer from '../components/Footer';

const Portfolio = () => {

    return(
        <div className="main-container">
            <Navigation />
            <div className="hintergrund-container">
                <h2>Unser Portfolio</h2>
                <div className="portfolio-header-container">
                    <p className="portfolio-beschreibung">
                        Hier können Sie sich einen Überblick über unsere aktuelle Arbeit verschaffen. So können Sie die
                        "Handschrift" des Entwicklers besser kennen lernen und sich ein eigenes Bild von der Agentur verschaffen.
                        Gerne stehen wir hier für auftretende Fragen telefonisch sowie per Nachricht zur Verfügung!
                    </p>
                </div>
                <div className="portfolio-footer-container">
                    <img src={StrichLinks} className="strich-links-portfolio"/>
                    <img src={StrichRechts} className="strich-rechts-portfolio"/>
                    <div className="portfolio-card-container">
                        <div className="portfolio-card">
                            <h3>BestWay</h3>
                            <img src={BestWay} alt=""/>
                            <p>Paket III</p>
                            <div className="button">
                                <Link to="/vorschau-bestway/navigation">Vorschau</Link>
                            </div>
                        </div>
                    </div>
                </div>
                <Footer />
            </div>
        </div>
    );
};

export default Portfolio;