import React from 'react';
import { Link } from 'react-router-dom';

// Images
import logo_dark from '../images/logo_dark.svg';

const Startseite = () => {
    return(
        <div className="startseite-container">
            <img src={logo_dark} className="logo-groß" alt="Großes CloudV Logo"/>
            <nav className="startseite-nav">
                <ul>
                    <li><Link to="/agentur">Agentur</Link></li>
                    <li><Link to="/portfolio">Portfolio</Link></li>
                    <li><Link to="/preise">Preise</Link></li>
                    <li><Link to="/kontakt">Kontakt</Link></li>
                </ul>
            </nav>
        </div>
    );
};

export default Startseite;