import React from 'react';

// Components
import VorschauNav from '../components/VorschauNav';

// Images
import BestWayStand from '../images/bestway-standort.PNG';

const BestWayStandort = () => {
    return(
        <div className="vorschau-container">
            <VorschauNav />
            <div className="background-second">
                <div className="vorschau-karte">
                    <h2>Vorschau - Standort</h2>
                    <img src={BestWayStand} alt=""/>
                </div>
            </div>
        </div>
    );
};

export default BestWayStandort;