import React from 'react';

// Components
import VorschauNav from '../components/VorschauNav';

// Images
import BestwayNavigation from '../images/bestway-nav.PNG';

const BestWayNav = () => {
    return(
        <div className="vorschau-container">
            <VorschauNav />
            <div className="background-second">
                <div className="vorschau-karte">
                    <h2>Vorschau - Navigation</h2>
                    <img src={BestwayNavigation} alt="Logo BestWay"/>
                </div>
            </div>
        </div>
    );
};

export default BestWayNav;