import React from 'react';
import { Switch, Route } from 'react-router-dom';

// Seiten
import Startseite from './pages/Startseite';
import Agentur from './pages/Agentur';
import Portfolio from './pages/Portfolio';
import Preise from './pages/Preise';
import Kontakt from './pages/Kontakt';
import BestWayNav from './pages/BestWayNav';
import BestWayProdukte from './pages/BestWayProdukte';
import BestWayStandort from './pages/BestWayStandort';

const App = () => {
  return(
    <div className="app">
      <Switch>
        <Route exact path="/" component={Startseite} />
        <Route path="/agentur" component={Agentur} />
        <Route path="/portfolio" component={Portfolio} />
        <Route path="/preise" component={Preise} />
        <Route path="/kontakt" component={Kontakt} />
        <Route path="/vorschau-bestway/navigation" component={BestWayNav} />
        <Route path="/vorschau-bestway/produkte" component={BestWayProdukte} />
        <Route path="/vorschau-bestway/standort" component={BestWayStandort} />
      </Switch>
    </div>
  );
};

export default App;